import React from 'react';
import styled from 'styled-components';
import Product_Card from './Product/Product';

const ProductPage = (props) => {

    const Products = props.Products.map((Product, index) => {
        return <Product_Card
            img={Product.img}
            key={Product.id} />
    })

    const Section = styled.section`
    padding: 120px;
    
    `;
    return (
        <Section>
            <div className="container  ">
                <h2 className="text-center">產品資訊</h2>
                <p className="text-center">
                    At vesro eos et accusamus et iusto odio dignissimos ducimus qui blanditiispraesentium</p>
                <div className="row">

                    {Products}

                </div>
            </div>
        </Section>
    );
}

export default ProductPage;